.Message{
    display: block;
    background-color: white;
    margin: 10px auto;
    box-shadow: 0 5px 15px -5px grey;
    padding: 10px 20px;
    border-radius: 5px;
    clear: both;
    width: 50%;
    &.me{
        color: white;
        float: right;
        background-color: #328ec4;
    }
}